// components/BlogList.js
import { Swiper, SwiperSlide } from "swiper/react";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { motion } from "framer-motion";
import { ArrowRight } from "lucide-react";
import { Navigation, Pagination, Autoplay } from "swiper/modules";
import { useState, useEffect, useCallback, useMemo } from "react";
import { Link } from "react-router-dom";
import Button from "../Button";
import toast from "react-hot-toast";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "react-lazy-load-image-component/src/effects/blur.css";

export default function BlogList() {
  const [blogs, setBlogs] = useState([]);
  const [loading, setLoading] = useState(true);

  const API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/blog/"
      : "https://allmaxsport.com/api/blog/";

  const fetchBlogs = useCallback(async () => {
    try {
      setLoading(true);
      const response = await fetch(API_URL);
      if (!response.ok)
        throw new Error(`HTTP error! Status: ${response.status}`);
      const data = await response.json();
      setBlogs(data);
    } catch (error) {
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchBlogs();
  }, [fetchBlogs]);

  return <Blog blogs={blogs} loading={loading} />;
}

function Blog({ blogs, loading }) {
  const swiperConfig = useMemo(
    () => ({
      spaceBetween: 24,
      slidesPerView: 1,
      navigation: true,
      pagination: { clickable: true },
      modules: [Navigation, Pagination, Autoplay],
      autoplay: { delay: 5000, disableOnInteraction: false },
      breakpoints: {
        640: { slidesPerView: 1 },
        768: { slidesPerView: 2 },
        1024: { slidesPerView: 3 },
      },
    }),
    []
  );

  return (
    <section
      className="relative py-20 px-4 md:px-10 bg-gradient-to-b from-blue-50/60 to-white overflow-hidden"
      dir="rtl"
    >
      <div className="absolute top-0 left-0 w-40 h-40 bg-blue-100 rounded-full blur-3xl opacity-30 animate-pulse"></div>

      <div className="max-w-7xl mx-auto relative z-10">
        {/* Heading */}
        <motion.div
          className="flex items-center justify-center gap-4 mb-16"
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          viewport={{ once: true }}
        >
          <div className="flex-1 h-px bg-gradient-to-r from-transparent via-blue-400 to-transparent"></div>
          <h2 className="text-4xl md:text-5xl font-bold text-gray-800 text-center relative">
            آخرین مقالات 
            <span className="block h-1 w-20 bg-blue-500 mx-auto mt-3 rounded-full"></span>
          </h2>
          <div className="flex-1 h-px bg-gradient-to-r from-transparent via-blue-400 to-transparent"></div>
        </motion.div>

        {/* Content */}
        {loading ? (
          <div className="flex justify-center items-center min-h-[300px]">
            <div className="h-12 w-12 border-4 border-blue-400 border-t-transparent rounded-full animate-spin"></div>
          </div>
        ) : blogs.length === 0 ? (
          <div className="text-center py-12">
            <p className="text-lg text-gray-600">هیچ بلاگی موجود نیست.</p>
          </div>
        ) : (
          <Swiper {...swiperConfig}>
            {blogs.map((blog) => (
              <SwiperSlide key={blog?.id}>
                <motion.article
                  className="bg-white rounded-3xl shadow-md overflow-hidden flex flex-col h-full border border-gray-200 group transition-all duration-300 hover:shadow-2xl hover:-translate-y-2"
                  whileHover={{ scale: 1.02 }}
                  transition={{ type: "spring", stiffness: 200 }}
                >
                  {/* Image */}
                  <div className="h-56 overflow-hidden">
                    <LazyLoadImage
                      src={blog.featured_image}
                      alt={blog.title}
                      className="object-cover w-full h-full transition-transform duration-700 group-hover:scale-110"
                      effect="blur"
                      loading="lazy"
                      wrapperClassName="w-full h-full"
                      placeholderSrc="data:image/svg+xml,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 300 200'%3E%3Crect width='300' height='200' fill='%23cccccc'/%3E%3C/svg%3E"
                    />
                  </div>

                  {/* Content */}
                  <div className="p-6 flex flex-col flex-1">
                    <h3 className="text-2xl font-semibold text-gray-800 mb-3 group-hover:text-blue-600 transition-colors duration-300">
                      {blog?.title}
                    </h3>
                    <p className="text-gray-600 mb-6 text-sm md:text-base line-clamp-3">
                      {blog?.excerpt}
                    </p>

                    <div className="mt-auto pt-4 border-t border-gray-200">
                      <Link
                        to={`/blog/${blog?.slug}`}
                        aria-label={`مطالعه مقاله ${blog?.title}`}
                      >
                        <Button className="w-full flex items-center justify-center gap-2 bg-gradient-to-r from-blue-500 to-blue-600 hover:from-blue-600 hover:to-blue-700 text-white font-semibold py-2 px-4 rounded-lg transition-all duration-300">
                          <span>مطالعه بیشتر</span>
                          <ArrowRight className="h-5 w-5" />
                        </Button>
                      </Link>
                    </div>
                  </div>
                </motion.article>
              </SwiperSlide>
            ))}
          </Swiper>
        )}
      </div>
    </section>
  );
}
